@import "../../styles/config";

.header {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
  display: flex;
  background: $header-footer-bg;
  padding: 3rem;
  background: $hd-gradient;

  .logo {
    font-size: 3rem;
    letter-spacing: .3rem;

    a {
      position: relative;
    }

    span {
      position: absolute;
      bottom: -1rem;
      right: 0;
      font-size: 1.2rem;
    }

  }

  .mob_menu, .mob_menu-opened {
    cursor: pointer;
    width: 4rem;
    background: $text-color;
    position: absolute;
    right: 3rem;
    top: 3rem;
    display: block;
    transition: all .3s;
  }

  .mob_menu {
    height: .5rem;

    &:before, &:after {
      content: '';
      background: $text-color;
      position: absolute;
      top: 1rem;
      right: 0;
      height: .5rem;
      width: 4rem;
    }

    &:after {
      top: -1rem;
    }
  }

  .mask-transition {
    background: rgba(0,0,0,.5);
    position: fixed;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
  }
}

.mob_menu-opened {
  height: 0;

  &:before, &:after {
    content: '';
    background: $text-color;
    position: absolute;
    transform: rotate(45deg);
    right: 0;
    top: 0;
    height: .5rem;
    width: 4rem;
  }

  &:after {
    transform: rotate(-45deg);
    top: 0;
  }
}

@media (max-width: 1245px) {
  .header {
    padding: 2.5rem;

    .logo {
      font-size: 2rem;
      letter-spacing: .2rem;
      margin-bottom: .5rem;

      span {
        font-size: 1rem;
      }
    }

    li + li {
      margin-left: .5rem;
    }
  }
}


@media (max-width: 767px) {
  .header {
    padding: 2rem;

    .logo {
      font-size: 1.8rem;
      letter-spacing: .1rem;
    }

    .mob_menu {
      top: 3rem;
      right: 1rem;
    }
  }
}