@import "styles/config.scss";

body {
  background-color: #000;
  background-image: url("images/bg.png");
  color: $text-color;
  font: 1.6rem 'Cinzel', serif;
  height: 100%;
  svg {
    fill: $text-color;
  }
}

#root {
  position: relative;
  min-height: 100%;
}

a {
  text-decoration: none;
  cursor: pointer;
  color: inherit;

  &:hover {
    opacity: .75;
  }
}

button {
  cursor: pointer;
  color: $text-color;
  background: transparent;
  border: .1rem solid $text-color;
  padding: .5rem 10rem;
}

ul:hover {
  svg, a {
    transition: all .5s;
    opacity: .65;
  }
}

li:hover {
  a, svg {
    opacity: 1;
  }
}


/*Reset CSS*/
html {
  box-sizing: border-box;
  height: 100%;
  font-size: 62.5%; /* 16px * 62.5 = 10px = 1rem */

}

*, *:before, *:after {
  box-sizing: inherit;
}

body, h1, h2, h3, h4, h5, h6, p, ol, ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

ol, ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

/*css library*/
.p-2 {
  padding: 2rem;
}

.error {
  color: red;
  font-size: 1.6rem;
}

.success {
  color: green;
  font-size: 1.6rem;
}

.error_message, .loading_message {
  font-size: 2rem;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loading_message {
  color: greenyellow;
}

.error_message {
  color: red;
}

